<template>
  <div>
    <Menu />
    <div class='container container-wide'>
      <PageHeader name='' action='/properties/new' />

      <div class='row'>
        <div class='col s12'>
          <Loader v-if='isLoading' is-fixed />
          <div v-else-if='isAlert' class='card blue-grey'>
            <div class='card-content white-text'>
              <span class='card-title'>     </span>
              <p>
                    /  
                   ,     
                  .
              </p>
            </div>
          </div>
          <Collection
            v-if='items.length > 0'
            @destroy='onDestroy'
            @hide='onHide'
          />

          <CollectionArchive
            @hide='onHide'
            @destroy='onDestroy'
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Collection from '@/components/properties/collection';
import CollectionArchive from '@/components/properties/collection_archive';
import Loader from '@/components/loader';
import Menu from '@/components/menu';
import PageHeader from '@/components/page_header';
import { get, call } from 'vuex-pathify';

export default {
  name: 'Properties',
  components: {
    Collection,
    CollectionArchive,
    Loader,
    Menu,
    PageHeader
  },
  props: {},
  data: () => ({}),
  computed: {
    token: get('user/token'),
    ...get('properties/*'),
    isAlert() {
      return !this.isLoading && this.items.length === 0;
    }
  },
  created() {
    this.fetch(this.token);
  },
  methods: {
    ...call([
      'properties/fetch',
      'properties/destroy',
      'properties/toggleIsHidden'
    ]),
    async onDestroy(property) {
      if (this.isDestroying) { return; }
      if (confirm(' .  ?')) {
        const res = await this.destroy({ token: this.token, property });
        const message = res != null ?
          ' ' :
          ' ';
        /* eslint-disable */
        M.toast({ html: message });
        /* eslint-enable */
      }
    },
    async onHide(property) {
      if (this.isSubmitting) { return; }

      const isHidden = await this.toggleIsHidden({ token: this.token, property });
      const message = isHidden ? '  ' : '  ';
      /* eslint-disable */ M.toast({ html: message }); /* eslint-enable */
    }
  }
};
</script>

